import Vue from 'vue'

const protocol = window.location.protocol;
const host = window.location.host;
const baseUrl = protocol + "//" + host;
var API_ROOT = process.env.NODE_ENV === 'production' ? window.serverConfig.dirServicePath : '/api';
// var API_ROOT = process.env.NODE_ENV === 'production'?baseUrl: '/api';
// var API_ROOT = process.env.NODE_ENV === 'production'?'': '/api';
export default {
  get(url, options, headers) {
    return new Promise((resolve, reject) => {
      Vue.prototype.$http.get(API_ROOT + url, options, headers).then((response) => {
        resolve(response.data);
      }).catch(function (error) {
        reject(error);
      })
    });
  },

  post(url, options, headers) {
    return new Promise((resolve, reject) => {
      Vue.prototype.$http.post(API_ROOT + url, options, { "headers": headers }).then((response) => {
        resolve(response.data);
      }).catch(function (error) {
        reject(error);
      })

    });
  },

  postXls(url, options, headers) {
    return new Promise((resolve, reject) => {
      Vue.prototype.$http.post(API_ROOT + url, options, headers).then((response) => {
        resolve(response.data);
      }).catch(function (error) {
        reject(error);
      })

    });
  },

  postParams(url, options) {
    return new Promise((resolve, reject) => {
      Vue.prototype.$http.post(API_ROOT + url, options, { 'Content-Type': 'application/x-www-form-urlencoded' }).then((response) => {
        resolve(response.data);
      }).catch(function (error) {
        reject(error);
      })

    });
  },

  delete(url, options) {
    return new Promise((resolve, reject) => {
      Vue.prototype.$http.delete(API_ROOT + url, options).then((response) => {
        resolve(response.data);
      }).catch(function (error) {
        reject(error);
      })

    });
  }
}




