<template>
  <div class="bg_body">
    <div>
      <div class="bar">
        <div class="bar-title">
          <span style="margin-left:25px">xx门户</span>
        </div>
        <div class="outLogin">
          <span
            ><img class="userIcon" src="../../static/img/user_icon.png" />{{
              userName
            }}</span
          >
        </div>
      </div>
      <div class="content">{{ userName }}欢迎访问</div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      userName: ""
    };
  },
  activated() {},
  mounted() {
    let _this = this;
    _this.userName = _this.$route.query.userName;
  }
};
</script>

<style scoped lang="less">
@import "../../static/css/index.less";

.content {
  height: 500px;
  line-height: 500px;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  color: #333;
}
</style>



