<template>
  <div class="loginContent">
    <div class="tablebox">
      <div id="imgbox">
        <span>{{ system_name }}</span>
      </div>
    </div>
    <div class="bgImg">
      <div class="bgLogin">
        <div class="login">
          <img
            class="logoLeft"
            src="../../static/img/uap6.0/login/loginLeft.png"
            alt=""
          />
          <img
            class="logoRight"
            src="../../static/img/uap6.0/login/loginRight.png"
            alt=""
          />

          <div class="panleCls">
            <!-- 口令 -->
            <el-tabs v-model="selectName" @tab-click="handleloginClick">
              <el-tab-pane label="协同签名认证" name="third">
                <div v-if="qrcodeShow" class="fd-code">
                  <!-- <div id="qrcode" ref="qrcode"></div> -->
                  <img :src="qrLoginUrl" alt="" id="qrcode" />
                  <p>请扫描二维码登录</p>
                </div>
                <p v-if="!qrcodeShow">{{ username }}已经登录</p>
              </el-tab-pane>
              <el-tab-pane label="用户口令认证" disabled> </el-tab-pane>
              <el-tab-pane label="短信认证" disabled> </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <div class="footer">xxx版权信息</div>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
export default {
  name: "index",
  data() {
    return {
      selectName: "third",
      // 显示二维码
      qrcodeShow: true,
      // 二维码客户端登录url(转换为二维码)
      qrLoginUrl: "",
      //二维码登录状态url(轮询url)
      qrLoginStateUrl: "",
      // 定时器群
      timer: null,
      // 用户名
      username: "",
      token: "",
      loading: false,
      system_name: "xxx门户"
    };
  },
  mounted() {
    if (this.timer) {
      clearInterval(this.timer);
    }
    document.title = "xxx门户";
    this.getUrl();
  },
  methods: {
    handleloginClick(tab) {
      if (this.timer) {
        clearInterval(this.timer);
      }
      if (tab.name == "third") {
        this.$refs.qrcode.innerHTML = "";
        this.getUrl();
      }
    },
    // 获取url
    getUrl() {
      let _this = this;
      let _data = {
        authType: "QRCodeAuth"
      };
      _this.API.post("/auth/api/v1/function/genLogin", _data)
        .then(res => {
          if (res.code === 20000) {
            _this.qrLoginUrl = "data:image/png;base64," + res.data.qrCode;
            _this.qrLoginStateUrl = res.data.qrLoginStateUrl;
            // this.qrcode();
            this.getStatus();
          }
        })
        .catch(err => {
          this.$message({
            message: "获取地址失败！",
            type: "warning"
          });
        });
    },
    // 轮休二维码登录状态
    getStatus() {
      let _this = this;
      let _authRandom = _this.qrLoginStateUrl.split("=")[1];
      let _data = {
        userContent: {
          authRandom: _authRandom
        }
      };
      _this.timer = setInterval(() => {
        _this.API.post("/auth/api/v1/function/qr_code", _data)
          .then(res => {
            if (res.code === 20000) {
              if (res.data.qrStatus === "-1") {
                this.$message({
                  message: "操作超时！",
                  type: "warning"
                });
              } else if (res.data.qrStatus === "0") {
                _this.token = res.data.token;
                clearInterval(_this.timer);
                _this.getUser();
              }
            }
          })
          .catch(() => {
            this.$message({
              message: "登录失败！",
              type: "warning"
            });
          });
      }, 2000);
    },
    // 单点跳转
    singleJump() {
      let _this = this;
      let _params = {
        appCode: "uaptest",
        token: _this.token
      };
      _this.API.post("/sso/api/v1.0/ssoToken/checkSsoToken", _params)
        .then(res => {
          if (res.code === 20000) {
            let _url =
              res.data.ssoConfig.ssoUrl +
              "?" +
              `${res.data.ssoTicket.key}=${res.data.ssoTicket.value}`;
            window.open(_url);
          }
        })
        .catch(() => {});
    },
    // 获取登录用户信息
    getUser() {
      let _this = this;
      let _data = {};
      let _header = {
        "AuthToken-Authorization": _this.token
      };
      _this.API.post("/auth/api/v1/function/getUserInfo", _data, _header)
        .then(res => {
          if (res.code === 20000) {
            _this.$message({
              message: res.data.userName + "登录成功！",
              type: "success"
            });
            _this.username = res.data.userName;
            _this.qrcodeShow = false;
            _this.$router.push({
              path: "/main",
              query: {
                userName: res.data.userName
              }
            });
          }
        })
        .catch(() => {});
    },
    // 生成二维码
    qrcode() {
      let qrcode = new QRCode("qrcode", {
        width: 200,
        height: 200,
        text: this.qrLoginUrl
      });
    }
  }
};
</script>
<style lang="less" scoped>
@import "../../static/css/login.less";
.fd-code {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 25%);
  text-align: center;
  p {
    margin-top: 20px;
  }
}
.bule_gray {
  margin-top: 10px !important;
  color: #333 !important;
}

.login .el-tabs__nav-wrap::after {
  background: none;
}

.login .el-tabs__active-bar {
  background-color: #1890ff;
  left: 39px;
  width: 85px !important;
}

.login .el-tabs__item {
  font-size: 20px;
  font-family: "MicrosoftYaHei-Bold";
}

.login .el-tabs__item.is-active {
  color: #1890ff;
}

.login-code-img {
  width: 90%;
  cursor: pointer;
  vertical-align: middle;
}

.formCodeIcon {
  width: 25px;
  margin-top: 10px;
}

.el-input__inner {
  height: 30px;
  line-height: 30px;
}

#qrcode {
  width: 200px;
  height: 200px;
}

.blackbgQr {
  background: rgba(0, 0, 0, 0.8);
  color: white;
  text-align: center;
  font-size: 20px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  line-height: 320px;
}

.codeInput {
  width: 55%;
}

/deep/.el-tabs__content {
  overflow: initial;
}
.figter {
  text-align: center;
  padding-top: 45px;
  img {
    width: 120px;
    height: 120px;
  }
}
.p-tip {
  font-size: 16px;
  font-weight: 300;
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>



